.aboutMeBody {
    background: rgb(143 125 107);
    min-height: 100vh;
    width: 100%;
}

.aboutMeBody {
    background: rgb(152, 126, 100);
    min-height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.aboutMeHeader {
    text-align: center;
    font-family: 'DM Serif Display', serif;
    font-size: 75px;
    letter-spacing: 12px;
    margin-block-start: 0px;
}

/* container for the pages effect and visible pages */
.stackedPagesContainer {
    font-family: 'Times New Roman', serif;
    position: relative;
    /* margin: auto; */
    margin-bottom: 5px;
    width: 90%;
    max-width: 1150px;
    border: 1px solid black;
    border-radius: 3px;
}

/* container for just the left and right page */
.spread {
    display: flex;
    position: relative;
    max-height: 650px;
    min-height: 500px;
    height: auto;
    width: calc(100% - 40px);
    /* Subtracts the width of the pageEdges */
}

.leftPage {
    width: 50%;
    height: auto;
    background-color: #fff;
    border-right: 2px solid black;
    box-shadow: 2px 1px 10px black;
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.rightPage {
    width: 50%;
    height: auto;
    background-color: #fff;
    border-right: 1px solid black;
    box-shadow: 1px 2px 10px -2px black;
    position: relative;
    z-index: 1;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.aboutMePgOne {
    font-family: 'Poppins', sans-serif;
    font-weight: 100;
    font-size: 70px;
    padding: 1px;
    text-align: center;
}

.gCanyon {
    height: 220px;
    width: 85%;
    margin: 30px auto;
}

.aboutMePgTwo {
    /* text-align: justify; */
    position: relative;
    padding: 1vw;
    line-height: 1.23;
    font-size: calc(0.3vw + 0.9em);
}

.pgTwoHeader {
    position: relative;
    top: -20px;
    text-align: center;
    font-family: 'DM Serif Display', serif;
    font-size: 30px;
    letter-spacing: 1px;
}

.horizontalLine {
    position: relative;
    top: -10px;
    width: 50%;
    height: 1px;
    background-color: black;
    margin: auto;
}

.youngBe {
    height: 90px;
    width: auto;
    margin-left: 1px;
    margin-right: 10px;

}

.aboutMePgTwoFirstP {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0.5vw 1.5vw;
    /* margin-bottom: -15px; */
}

.aboutMePgTwoSecondP {
    margin: 0.5vw 1.5vw;
}

.aboutMePgTwoThirdP {
    margin: 0.5vw 1.5vw;
}


.aboutMePgThreeFirstP {
    font-size: calc(0.3vw + 0.9em);
    display: flex;
    flex-direction: column;
    margin: 1vw 5vw;
}

.medicFigure {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    font-size: 14px;
}

.medic {
    height: auto;
    width: 50%;
    margin-bottom: 10px;
}

.electronics {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1px;
    height: 60dvh;
    width: auto;
}

.pageEdges {
    position: absolute;
    top: 0px;
    right: 0px;
    height: 100%;
    width: 40px;
    background-color: #dfdfdf;
}

/* these are the lines meant to replicate pages */
.pageEdge {
    position: absolute;
    right: -1px;
    height: 100%;
    width: 1px;
    background-color: #f8f8f8;
    /* box-shadow: 1px 0 1px rgba(61, 61, 61, 0.2); */
}

.aboutMeButtons {
    display: flex;
    gap: 13px;
}

.aboutMeHomeButton {
    height: 40px;
    letter-spacing: 2px;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 18px;
    border: 2px solid;
    padding: 0.25em 0.5em;
    box-shadow: 0px 2px 0px 0px;
    cursor: pointer;

}

.leftPageArrow,
.rightPageArrow {
    height: 40px;
    font-size: 20px;
    text-decoration: none;
    text-transform: uppercase;
    color: #000;
    cursor: pointer;
    border: 2px solid;
    padding: 0.25em 0.5em;
    box-shadow:
        -1px 1px 0px 0px,
        -2px 2px 0px 0px,
        -3px 2px 0px 0px;
    /* -4px 4px 0px 0px;  */
    /* -5px 5px 0px 0px; */
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
}

.rightPageArrow {
    box-shadow:
        1px 1px 0px 0px,
        2px 2px 0px 0px,
        3px 2px 0px 0px;
    /* 4px 4px 0px 0px;  */
    /* 5px 5px 0px 0px; */
}

.leftPageArrow:active {
    box-shadow: 0px 0px 0px 0px;
    top: 5px;
    left: 5px;
}

.rightPageArrow:active {
    box-shadow: 0px 0px 0px 0px;
    top: 5px;
    left: 5px;
}

@media (min-width: 768px) {
    .aboutMeButtons {
        padding: 0.25em 0.75em;
    }
}

.pageEdge:nth-child(1) {
    right: 1px;
}

.pageEdge:nth-child(2) {
    right: 6px;
}

.pageEdge:nth-child(3) {
    right: 11px;
}

.pageEdge:nth-child(4) {
    right: 16px;
}

.pageEdge:nth-child(5) {
    right: 21px;
}

.pageEdge:nth-child(6) {
    right: 26px;
}

.pageEdge:nth-child(7) {
    right: 31px;
}

.pageEdge:nth-child(8) {
    right: 36px;
}

